@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200&family=Viga&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
* {
  box-sizing: border-box;
}

.root {
  display: flex;
  flex-direction: column;
}
/* #22254b */

body {
  background-color: #040309;
  color: white;
  font-family: "Poppins", sans-serif;
  margin: 0;
}

.Navbar {
  background-color: #0a0d1d;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0.3rem;
  z-index: 1;
  position: fixed;
  width: 100%;
  /* justify-content: center; */
  /* cursor: pointer; */
}

.logo {
  color: black;
  font-family: "Oswald", sans-serif;
  text-decoration: none;
  font-size: 20px;
  position: relative;
  top: 2px;
  background-color: rgb(243, 174, 46);
  padding: 3px 5px;
  border-radius: 8px;
  /* margin-right: 1150px; */
  font-weight: bolder;
  cursor: pointer;
}

.search {
  font-family: inherit;
  font-size: 0.8rem;
  color: white;
  border: 2px solid #22254b;
  border-radius: 50px;
  background-color: transparent;
  padding: 0.3rem 1.2rem;
  transition: font-size 0.3s ease-in-out;
}

.search:focus {
  border: 1px solid white;
  color: white;
  font-size: 0.9rem;
  outline: none;
}

.movie-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
/* 373b69 */
.movie {
  background-color: #0a0d1d;
  margin: 1rem;
  width: 250px;
  border-radius: 3px;
  box-shadow: 3px 3px 5px rbga(0, 0, 0, 0.1);
  overflow: hidden;
  position: relative;
  height: 550px;
}

.movie img {
  object-fit: cover;
  width: 100%;
  height: 400px;
}

.movie-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
}
.movie-info h4 {
  /* font-size: 20px; */
  /* margin-right: 15px; */
  margin: 0;
  color: #040309;
}
.movie-over {
  padding: 1rem;
  background-color: white;
  color: #040309;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  box-shadow: 0 -3px 5px rbga(0, 0, 0, 0.1);
  position: absolute;
  bottom: 0;
  top: 0;
  right: 0;
  overflow: auto;
  max-height: 100%;
  transform: translateY(102%);
  transition: transform 0.3s ease-in-out;
}

.movie:hover .movie-over {
  transform: translateY(0%);
}

.over-more {
  display: flex;
  justify-content: space-between;
}
.over-more button {
  border: none;
  outline: none;
  height: 100%;
  padding: 8px;
  /* margin: 25px 10px; */
  background-color: #0a0d1d;
  color: white;
  font-size: 15px;
  font-weight: bolder;
  border-radius: 5px;
  font-family: "poppins", sans-serif;
  cursor: pointer;
}
.over-text {
  font-weight: bold;
}
.over-title {
  font-size: 1.2rem;
  font-weight: bold;
  text-decoration: underline;
}

.tag {
  background-color: #22254b;
  padding: 0.5rem;
  font-weight: bolder;
  border-radius: 5px;
}

.tag.green {
  color: rgb(41, 189, 41);
}
.tag.orange {
  color: orange;
}
.tag.red {
  color: red;
}

.copyright {
  display: flex;
  flex-shrink: 0;
  justify-content: center;
}
.copyright span {
  color: white;
  opacity: 90%;
}
.results-show {
  margin-top: 20px;
}
.pages {
  display: flex;
  justify-content: flex-end;
  margin-right: 45px;
}
.pages a {
  border: none;
  outline: none;
  height: 100%;
  padding: 5px 10px;
  margin: 15px 5px;
  background-color: #0a0d1d;
  color: white;
  font-size: 15px;
  font-weight: bolder;
  border-radius: 5px;
  font-family: "poppins", sans-serif;
  cursor: pointer;
  text-decoration: none;
}

.dash{
  width:50px;
  height: 5px;
  background-color: white;
  transform: rotateX(90deg);
}
.new-rel-text{
  display: flex;
  /* margin-top: 15px; */
  margin-bottom: -10px;
  margin-left: 0.4rem;
  border-left: white;
  font-family:  "Oswald",sans-serif;
  color: rgb(243, 174, 46);
  font-size: 2rem;
  letter-spacing: 1px;
}
.res{
  margin-left: 2.7rem;
  color: white;
  font-size: 1.5rem;
}
.degline{
  margin-left: 2.7rem;
  width: 8px;
  height: 50px;
  position: relative;
  cursor: pointer;
  background: rgb(243, 174, 46);
}

.actions{
  position: relative;
  top: 2px;
}

.actions a{
  top: 5px;
  color: rgb(219, 203, 203);
  text-decoration: none;
}

.nav-links{
  transition: all 0.3s ease-in-out;
}

.nav-links:hover{
  background-color: rgb(243, 174, 46);
  padding: 0.1rem 0.2rem;
  border-radius: 4px;
  color: #040309;
  font-size: 1rem;
  font-weight:bolder;
}

.actions{
  cursor: pointer;
}

.box-head{
  width: 100%;
  display: flex;
  justify-content: space-around;
}
.search-icon:hover{
  background-color: #040309;
 padding: 5px 1px;
 border-radius: 10px;
 margin-left: 5px;

}


@media (max-width: 520px) {
  .search {
    width: 150px;
    height: 100%;
  }
  .logo{
    font-size: 10px;
    position: relative;
    top: 15px;
  }
}

@media (max-width: 420px) {
  .search-icon {
    display: none;
  }
  .search {
    position: relative;
    bottom: 2px;
    transition: none;
    width: 150px;
    height: 100%;
  }
  .search:focus {
    border: 1px solid white;
    color: white;
    outline: none;
  }
  .logo {
    position: relative;
    top: 10px;
    font-size: 10px;
  }
  .movie-icon {
    display: none;
  }
  .Navbar{
    display: flex;
    justify-content: space-evenly;
  }
  header{
    margin-top: 5px;
  }
}

.logo {
  color: black;
  font-family: "poppins", sans-serif;
  text-decoration: none;
  font-size: 20px;
  position: relative;
  top: 2px;
  background-color: rgb(243, 174, 46);
  padding: 3px 5px;
  border-radius: 8px;

  cursor: pointer;
}

@media (max-width: 650px) {
  .pages {
    display: flex;
    justify-content: center;
    margin-right: 0;
  }
  
}
@media (max-width: 780px) {
  .actions{
    display: none;
  }
  
}
@media (max-width: 420px) {
  .logo{
    top: 8px;
  }
}




